@import "../../assets/style/colors.scss";

.Location {
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;

  &-EditableFields {
    display: grid;
    grid-template-columns: max-content fit-content(224px);
    grid-auto-rows: auto;
    column-gap: 10px;

    & > span {
      font-weight: 300;
      font-size: 14px;
      line-height: 16px;
      color: $MainBlack;
      text-align: right;
    }
  }

  &-Map {
    display: flex;
    flex-direction: column;
    box-sizing: border-box;
    margin-top: 32px;
    height: 100%;

    & > span {
      display: inline-block;
      margin-bottom: 16px;
      font-weight: 300;
      font-size: 14px;
      line-height: 16px;
      color: $MainBlack;
    }

    @media (max-width: 1023px) {
      height: 100%;
    }

    @media (max-width: 767px) {
      padding: 0;
      padding-top: 16px;
      height: 320px;
    }

    width: 100%;
    max-width: 736px;

    &-MapBox {
      width: 99%;
      min-height: 400px;
    }
  }
}
