@import "../../assets/style/colors.scss";

.AdminButton {
  background: $AdminMainBlue;
  border: 0.5px solid $AdminMainBlue;
  box-sizing: border-box;
  border-radius: 4px;

  font-weight: normal;
  font-size: 11.5px;
  line-height: 13px;
  letter-spacing: -0.0821429px;

  color: #ffffff;
  height: 29px;
  cursor: pointer;

  &_decline{
    background:$AdminDecline;
  }
}
